<template>
  <div>
    <Modal v-model="createCouponModal" width="400">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>Create Coupon</span>
      </p>
      <Form :model="createCouponForm" style="margin:auto">
        <FormItem>
          <Input
          v-model="createCouponForm.coupon_id"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="1"
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem>
          <Input
          v-model="createCouponForm.discount"
          type="number"
          placeholder="Enter something..."
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem>
          <Input
          v-model="createCouponForm.inventory"
          type="number"
          placeholder="Enter something..."
          :autosize="{minRows: 1}"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
      	<Button type="primary" size="large" long @click="submit()">Submit</Button>
      </div>
    </Modal>
    <h1 style="margin-top:10px; text-align: center;">Coupon</h1>
    <Table height="500" border :columns='couponColumns' :data='couponData' style="margin-top: 20px;">
      <template slot-scope="{ row, index }" slot="coupon_id">
        <Input type="text" v-model="editCoupon_id" v-if="editIndex === index" />
        <span v-else>{{ row.coupon_id }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="discount">
        <Input type="number" v-model="editDiscount" v-if="editIndex === index" />
        <span v-else>{{ row.discount }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="inventory">
        <Input type="number" v-model="editInventory" v-if="editIndex === index" />
        <span v-else>{{ row.inventory }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="action">
        <div v-if="editIndex === index">
          <Button type="success" size="small" style="margin-right: 5px" @click="handleSave(index)">保存</Button>
          <Button type="warning" size="small" @click="editIndex = -1">取消</Button>
        </div>
        <div v-else>
          <Button type="primary" size="small" style="margin-right: 5px" @click="handleEdit(row, index)">修改</Button>
          <Button type="error" size="small" @click="remove(index)">移除</Button>
        </div>
      </template>
    </Table>
    <Button type="primary" style="margin-top: 10px; width: 100px;" v-on:click="createCoupon">
      <Icon type="ios-add" size="20"></Icon>
      Add
    </Button>
    <Button type="info" style="margin-top: 10px; margin-left: 10px; width: 100px;" v-on:click="upLoad">
      <Icon type="ios-cloud" size="20"></Icon>
      Upload
    </Button>
  </div>
</template>

<script>
  //import data from '../mock/coupon.js'
  export default {
    props:{
      dataSource: Number
    },
    data() {
      return {
        createCouponModal: false,
        createCouponForm: {
          coupon_id: '',
          discount: 0,
          inventory: 0
        },
        couponColumns: [{
            title: 'coupon_id',
            //slot: 'coupon_id',
            key: 'coupon_id',
            sortable: true
          },
          {
            title: 'discount',
            slot: 'discount',
            sortable: true,
            filters: [{
                label: '0 ~ 20',
                value: 1
              },
              {
                label: '20 ~ 50',
                value: 2
              },
              {
                label: '50 ~ 100',
                value: 3
              }
            ],
            filterMultiple: false,
            filterMethod(value, row) {
              if (value === 1) {
                return row.discount <= 20;
              } else if (value === 2) {
                return row.discount > 20 && row.discount <= 50;
              } else {
                return row.discount > 50;
              }
            }
          },
          {
            title: 'inventory',
            slot: 'inventory',
            sortable: true,
            filters: [{
                label: '0 ~ 2000',
                value: 1
              },
              {
                label: '2000 ~ 5000',
                value: 2
              },
              {
                label: '5000 ~ 10000',
                value: 3
              }
            ],
            filterMultiple: false,
            filterMethod(value, row) {
              if (value === 1) {
                return row.inventory <= 2000;
              } else if (value === 2) {
                return row.discount > 2000 && row.discount <= 5000;
              } else {
                return row.discount > 5000;
              }
            }
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center'
          }
        ],
        couponData: [],
        editIndex: -1, // 当前聚焦的输入框的行数
        editCoupon_id: '',
        editDiscount: 0,
        editInventory: 0
      }
    },
    mounted() {
      var url
      if(this.dataSource == 1){
        url = '/coupon/info'
      }
      else {
        url = '/g/coupon/info'
      }
      this.$axios
        .get(url)
        .then(response => {
          this.couponData = response.data
          this.$Message.success('成功加载Coupon数据')
        })
        .catch(error => {
          this.$Message.error('加载Coupon数据失败')
        })
    },
    methods: {
      upLoad(){
        var url
        if(this.dataSource == 1){
          url = 'coupon/update'
        }
        else {
          url = '/g/coupon/update'
        }
        this.$axios
            .post(url, {
              list: this.couponData
            })
            .then(response => {
              this.$Message.success("成功将Coupon数据同步至数据库")
            })
            .catch(error => {
              this.Message.error("同步Coupon数据失败！")
            })
      },
      submit(){
        const newCoupon = {
          coupon_id: this.createCouponForm.coupon_id,
          discount: this.createCouponForm.discount,
          inventory: this.createCouponForm.inventory,
        }
        this.couponData.push(newCoupon)
        this.createCouponModal = false
      },
      createCoupon(){
        this.createCouponModal = true
      },
      remove(index) {
        this.couponData.splice(index, 1);
      },
      handleEdit(row, index) {
        this.editCoupon_id = row.coupon_id;
        this.editDiscount = row.discount;
        this.editInventory = row.inventory;
        this.editIndex = index;
      },
      handleSave(index) {
        this.couponData[index].coupon_id = this.editCoupon_id;
        this.couponData[index].discount = this.editDiscount;
        this.couponData[index].inventory = this.editInventory;
        this.editIndex = -1;
      }
    }
  }
</script>

<style>
</style>



